.nav{
    width: 75%;
    height: 100%;
    background-color: white;
    position: fixed;
    z-index: 999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.links{
    list-style: none;
    align-content: center;
    margin: 0 auto;
    padding: 0;
}
.li{
    display: block;
    font-family: "Argent Medium";
    color: #414143;
    cursor: pointer;
    margin-bottom: 1rem;
  }