@font-face{
    font-family: "Argent";
    src: local("ArgentumSans"),
      url("../fonts/ArgentumSans-BlackItalic.ttf") format("truetype");
    font-weight: 300;
  }
  
  @font-face{
    font-family: "Argent Medium";
    src: local("ArgentumSans"),
      url("../fonts/ArgentumSans-Medium.ttf") format("truetype");
    font-weight: 300;
  }

  @font-face{
    font-family: "Argent Light";
    src: local("ArgentumSans"),
      url("../fonts/ArgentumSans-Light.ttf") format("truetype");
    font-weight: 300;
  }
.contact{
    width: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 50;
    position: absolute;
    top: 5rem;
}
.content{
    width: 80%;
    display: grid;
    grid-template-columns: 60% 40%;
    justify-content: center;
    margin-bottom: 2rem;
}
.h1{
    font-family: Argent;
    font-size: 3rem;
    color: #df4426;
}
.form{
    width: 45vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-right: 2rem;
    
}
.input{
    width: 100%;
    height: 3rem;
    margin-bottom: .5rem;
}
.input::placeholder {
    color: #414143;
    font-family: 'Argent Medium';
    object-position: top left;
    padding-left: 1rem;
}

::-webkit-input-placeholder { opacity: 1; -webkit-transition: opacity .5s; transition: opacity .5s; }  /* Chrome <=56, Safari < 10 */
:-moz-placeholder { opacity: 1; -moz-transition: opacity .5s; transition: opacity .5s; } /* FF 4-18 */
::-moz-placeholder { opacity: 1; -moz-transition: opacity .5s; transition: opacity .5s; } /* FF 19-51 */
:-ms-input-placeholder { opacity: 1; -ms-transition: opacity .5s; transition: opacity .5s; } /* IE 10+ */
::placeholder { opacity: 1; transition: opacity .5s; } /* Modern Browsers */
    
*:focus::-webkit-input-placeholder { opacity: 0; } /* Chrome <=56, Safari < 10 */
*:focus:-moz-placeholder { opacity: 0; } /* FF 4-18 */
*:focus::-moz-placeholder { opacity: 0; } /* FF 19-50 */
*:focus:-ms-input-placeholder { opacity: 0; } /* IE 10+ */
*:focus::placeholder { opacity: 0; } /* Modern Browsers */
.check{
    display: flex;
    flex-direction: row;
    margin: .5rem;
    margin-left: 0;
    align-items: center;
}
.msgInput{
    width: 100%;
    height: 60%;
    margin-bottom: .5rem;
}
.msgInput::placeholder {
    color: #414143;
    font-family: 'Argent Medium';
    line-height: 1rem;
    padding: 1rem;
}
.submit{
    width: 101%;
    height: 3rem;
    background-color:#df4426;
    color: white;
    font-family: Argent;
    margin: 0;
}
.details{
    height: 100%;
    display: flex;
    justify-content: left;
    flex-direction: column;
}
.title{
    font-family: 'Argent Medium';
    font-size: 1.5rem;
    color: #df4426;
    margin-top: 0;
    margin-bottom: 1rem;
}
.subtext{
    font-size: 1.05rem;
    color: #414143;
    margin-top: 0;
    margin-bottom: .75rem;
    font-family: 'Argent Light';
}


.service{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 5rem;
}
.serviceContainer{
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-areas: 
        "ft gt"
        "am pm"
        "amt pmt"
        
        ;
    justify-content: center;
    object-position: center;
    overflow-x: hidden;
    justify-items: center;
}
.imgContainer{
    width: 45vw;
    height: 15rem;
    overflow: hidden;
    filter: drop-shadow(5px 5px 4px #414143be);
    margin-bottom: 1.5rem;
}
.textContainer{
    width: 45vw;
    display: flex;
    overflow: hidden;
    margin-top: -1.5rem;
    margin-bottom: 1.5rem;
    text-align: center;
    font-size: 1.15rem;
    font-family: 'Argent Light';
    background-color:#df4526e1;
    filter: drop-shadow(5px 5px 4px #414143be);
    align-items: center;
    color: white;
}
.transport{
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    
  }
.serviceName{
    font-family: Argent;
    font-size: 2rem;
    color: white;
    opacity: .75;
    position: absolute;
    bottom: 0%;
    margin-left: 2rem;
    filter: drop-shadow(5px 5px 4px #414143);
}
.src{
    object-fit: cover;
}
.footer{
    width: 100vw;
    height: 35vh;
    background-color: #414143;
    
    
    bottom: 0;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-template-areas: 
      "title title title"
      "copy local res"
      "num num num"
      ;
    align-content: center;
    align-items: center;
    justify-items: center;
  }
