@font-face{
  font-family: "Argent";
  src: local("ArgentumSans"),
    url("./fonts/ArgentumSans-BlackItalic.ttf") format("truetype");
  font-weight: 300;
}

@font-face{
  font-family: "Argent Medium";
  src: local("ArgentumSans"),
    url("./fonts/ArgentumSans-Medium.ttf") format("truetype");
  font-weight: 300;
}
::-webkit-scrollbar {
  visibility: hidden;
  width: 0;
}
html, body {
  max-width: 100%;
  overflow-x: hidden;
}
.pagecontainer{
  width: 100%;
  
}
.App {
  width: 100%;
  height: 250vh;
  overflow-x: hidden;
}

.hero{
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  z-index: -1;
}
.video{
  object-fit: cover;
}
.nav{
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  overflow: hidden;
  position: fixed;
  z-index: 1001;
  background-color: transparent;
}
.navActive{
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  overflow: hidden;
  position: fixed;
  z-index: 1001;
  background-color: rgba(255, 255, 255, 0.733);
  
}
.logo{
  width: 15%;
  margin: 0;
  margin-left: 1rem;
  margin-top: 1rem;
  z-index: 1001;
}
.burger{
  display: none;
}
.navlinks{
  margin-right: 2rem;
}
.li{
  display: inline;
  margin-left: 1rem;
  font-family: "Argent Medium";
  color: rgb(240, 240, 240);
  
  cursor: pointer;
}
.li:visited{
  color: white;
  text-decoration: none;
}
.heroContent{
  width: 100%;
  height: 100vh;
  align-content: center;
}
.tagContainer{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 2rem;
}
.tagline{
  margin-top: 25%;
  color: white;
  font-size: 3rem;
  font-family: Argent;
  filter: drop-shadow(.5rem .5rem .25rem rgba(0, 0, 0, 0.178));
}
.leaders{
  width: 100%;
  height: 50%;
  background-color: white;
  position: absolute;
  top: 100vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-areas: 
    "title pic"
    
  ;
  justify-content: center;
}
.leaderInfo{
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-area: title;
  margin-top: 1rem;
}
.leadership{
  font-family: Argent;
  font-size: 3rem;
  margin-left: 2rem;
  color: #df4426;
  margin-bottom: 0;
  filter: drop-shadow(.25rem .25rem .25rem rgba(0, 0, 0, 0.192));
}
.vpName{
  font-family: Argent;
  font-size: 2rem;
  margin-top: -.1rem;
  color: #414143;
}
.vp{
  height: 100%;
  grid-area: pic;
}
.vpinfo{
  margin-top: .5rem;
  margin-bottom: 0;
  font-size: 1.25rem;
  font-family: 'Argent Medium';
}
.parallax{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 150vh;
  overflow: hidden;
  justify-content: center;
}
.welcomeContainer{
  width: 100%;
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
}
.welcomeTitle{
  font-family: Argent;
  font-size: 20vw;
  color: rgba(255, 255, 255, 0.808);
  margin-bottom: 0;
  margin-top: 2rem;
  margin-left: 150%;
  letter-spacing: -.5rem;
  filter: blur(5px);
  filter: drop-shadow(.5rem .5rem .25rem rgba(0, 0, 0, 0.336));
}
.welcome{
  font-family: "Argent Medium";
  font-size: 1.5rem;
  color: white;
  width: 70%;
  margin-top: 40%;
  position: absolute;
  filter: blur(5px);
  filter: drop-shadow(.25rem .25rem .25rem rgba(0, 0, 0, 0.616));
}
.pbackground{
  width: 100%;
  height: 100%;
  z-index: -2;
  position: absolute;
  object-fit: cover;
  
  filter: saturate(.5);
}
.pforeground{
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  margin-left: 0;
  object-fit: cover;
  
  filter: saturate(.5);
}
.customer{
  width: 100%;
  height: 60%;
  background-color: white;
  position: absolute;
  top: 250vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.customerTitle{
  font-family: Argent;
  font-size: 3rem;
  color: #df4426;
  filter: drop-shadow(.25rem .25rem .25rem rgba(0, 0, 0, 0.192));
  justify-items: center;
  grid-area: title;
  margin-top: 0;
}
.customerContainer{
  width: 100%;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-template-areas: 
    "title title title"
    "coy jb rob"
  ;
  align-items: center;
  justify-items: center;
}
.coyote{
  width: 33%;
  grid-area: coy;
  margin: 0;
}
.jbhunt{
  width: 33%;
  grid-area: jb;
  margin: 0;
}
.robinson{
  width: 33%;
  grid-area: rob;
  margin: 0;
}
.footer{
  width: 100%;
  height: 35%;
  background-color: #414143;
  overflow: hidden;
  position: absolute;
  top: 310vh;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-template-areas: 
    "title title title"
    "copy local res"
    "num num num"
    ;
  align-content: center;
  align-items: center;
  justify-items: center;
}
.copy{
  font-family: "Argent Medium";
  grid-area: copy;
  margin: 0;
  color: white;
}
.reserved{
  font-family: "Argent Medium";
  grid-area: res;
  margin: 0;
  color: white;
}
.footName{
  font-family: "Argent Medium";
  grid-area: title;
  margin: 0;
  color: white;
  margin-bottom: .5rem;
}
.location{
  font-family: "Argent Medium";
  grid-area: local;
  margin: 0;
  margin-bottom: .5rem;
  color: white;
}
.number{
  font-family: "Argent Medium";
  grid-area: num;
  margin: 0;
  margin-bottom: .25rem;
  color: white;
}

@media screen and (max-width: 900px) {
  .hero{
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: absolute;
    object-position: 75%;
    z-index: -1;
  }
  .tagline{
    margin-top: 50%;
    color: white;
    font-size: 3rem;
    font-family: Argent;
    filter: drop-shadow(.5rem .5rem .25rem rgba(0, 0, 0, 0.178));
  }
  .logo{
    width: 35%;
    margin: 0;
    margin-left: 2rem;
    margin-top: 1rem;
    z-index: 1001;
  }
  .burger{
    width: 10%;
    background-color: transparent;
    border: none;
    overflow: hidden;
    margin-right: 2rem;
    display: block;
  }
  .navlinks{
    display: none;
  }
  .leaders{
    width: 100%;
    height: 50vh;
