    margin-bottom: 0;
    z-index: 1;
    filter: drop-shadow(.25rem .25rem .25rem rgba(0, 0, 0, 0.192));
  }
  .vpName{
    font-family: Argent;
    font-size: 1.5rem;
    margin-top: -.1rem;
    margin-left: 1.5rem;
    color: #414143;
  }
  .vp{
    height: 100%;
    grid-area: pic;
    margin-left: -20%;
  }
  .vpinfo{
    margin-top: .5rem;
    margin-bottom: 0;
    margin-left: 1rem;
    font-size: 1rem;
    font-family: 'Argent Medium';
    text-align: center;
  }
  .parallax{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 150vh;
    overflow: hidden;
