    background-color: #414143;
  }
  .welcomeTitle{
    font-family: Argent;
    font-size: 15rem;
    color: rgba(255, 255, 255, 0.808);
    margin-bottom: 0;
    margin-top: 2rem;
    margin-left: 275%;
    letter-spacing: -.5rem;
    filter: blur(5px);
    filter: drop-shadow(.5rem .5rem .25rem rgba(0, 0, 0, 0.336));
  }
  .welcome{
    font-family: "Argent Medium";
    font-size: 1.25rem;
    color: white;
    width: 90%;
